import NextAuth, { CredentialsSignin } from "next-auth";
import Credentials from "next-auth/providers/credentials";
import GitHub from "next-auth/providers/github";
import connectDB from "./lib/db";
import { User } from "./lib/schema";
import { compare } from "bcryptjs";

export const { handlers, signIn, signOut, auth } = NextAuth({
    providers: [
        Credentials({
            name: "Credentials",
            credentials: {
                email: { label: "Email" },
                password: { label: "Password", type: "password" },
            },
            async authorize(credentials) {
                console.log("credentials", credentials);
                const { email, password } = credentials;
                if (!email || !password) {
                    throw new CredentialsSignin(" .");
                }

                // DB 
                await connectDB();
                const user = await User.findOne({ email }).select("+password +role");
                if (!user) {
                    throw new CredentialsSignin("  .");
                }

                //        
                const isMacthed = await compare(String(password), user.password);
                if (!isMacthed) {
                    throw new CredentialsSignin("  .");
                }

                //  !!!
                return {
                    name: user.name,
                    email: user.email,
                    role: user.role,
                    id: user._id, // _id MongoDB _id // ObjectId("")
                };
            },
        }),
        GitHub({
            clientId: process.env.GITHUB_CLIENT_ID,
            clientSecret: process.env.GITHUB_CLIENT_SECRET,
        }),
    ],
    callbacks: {
        signIn: async ({ user, account }: { user: any; account: any }) => {
            console.log("signIn", user, account);
            if (account?.provider === "github") {
                //  ..
                const { name, email } = user;
                await connectDB(); // mongodb 
                const existingUser = await User.findOne({ authProviderId: user.id });
                if (!existingUser) {
                    //  
                    await new User({
                        name,
                        email,
                        authProviderId: user.id,
                        role: "user",
                    }).save();
                }

                const socialUser = await User.findOne({ authProviderId: user.id });

                user.role = existingUser?.role || "user";
                user.id = existingUser?._id || null;

                return true;
            } else {
                //  
                return true;
            }
        }, // Credentials GitHub    
        async jwt({ token, user }: { token: any; user: any }) {
            console.log("jwt", token, user);
            if (user) {
                token.role = user.role; // JWT    
                token.id = user.id; // JWT   ID 
            }
            return token;
        },
        async session({ session, token }: { session: any; token: any }) {
            if (token?.role) {
                session.user.role = token.role;
                session.user.id = token.id;
            }
            return session;
        },
    },
});
